        {icon: require("@/assets/icon2.png"), name: "幸福西饼", link: "/mall/category?id=3"},
        {icon: require("@/assets/icon3.png"), name: "礼品定制", link: "/mall/category?id=4"},
        {icon: require("@/assets/icon5.png"), name: "充值中心", link: "/recharge"},
        {icon: require("@/assets/icon4.png"), name: "积分转让", link: "/my/exchange"},
