        {icon: require("@/assets/icon9.png"), name: "购物卡", link: "/mall/list?cid=106"},
      ],
      list: []
    });
    return {state};
  },

  created() {
    let arr = this.config.banner.split(",");
    if (arr.length > 0){
      this.state.swipe = [];
      for (let i in arr) {
        this.state.swipe.push({image: arr[i]});
      } 
    }
    
    this.$http.get("/mall/itemlist?recommend=1&limit=10").then((data) => {
      this.state.list = this.state.list.concat(data);
    });
  },

  computed: {
    color1() {
      return this.state.btc.rate >= 0 ? "#090" : "#F66";
    },
    color2() {
      return this.state.eth.rate >= 0 ? "#090" : "#F66";
    },
    ...mapState(["config", "profile"]),
    ...mapGetters(["authorized"]),
  },

  methods: {

  }

}
</script>
